}

a {
  color: inherit;
}

.accordion-header {
  position: relative;
  border-bottom: 1px solid #ccc;
}
.accordion-trigger {
  display: inline-block;
  border: none;
  margin: 0;
  text-decoration: none;
  cursor: pointer;
  line-height: 1;
  box-shadow: none;
  appearance: none;
  display: block;
  font-size: 1.875rem;
  font-weight: 700;
  text-align: left;
  background-color: #ddd;
  width: 100%;
  padding: 1.25rem;
  box-sizing: border-box;
  border: 2px solid #ddd;
}
.accordion-trigger:hover {
  background-color: #ccc;
  border: 2px solid #ccc;
}
.accordion-trigger:focus-visible {
  outline: none;
  border: 2px solid #087bef;
}
.accordion-panel {
  height: 0;
}
.accordion-content {
  padding: 1.25rem;
  opacity: 0;
}
.accordion-panel.expanded > div {
  opacity: 1;
}
.accordion-panel.animate-in {
  transition: height 0.3s ease-out;
}
.accordion-panel.animate-in > div {
  transition: opacity 0.4s ease-out 0.2s;
}
.accordion-panel.animate-out {
  transition: height 0.3s ease-out 0.2s;
}
.accordion-panel.animate-out > div {
  transition: opacity 0.3s ease-out;
}

.button {
  display: inline-block;
  border: none;
  margin: 0;
  text-decoration: none;
  cursor: pointer;
  line-height: 1;
  box-shadow: none;
  appearance: none;
  border-radius: 3px;
  background-color: #00a555;
  color: #fff;
  padding: 0.8125rem 0.625rem;
  font-size: 0.875rem;
  font-family: "IBM Plex Sans Thai Looped", sans-serif;
  font-weight: 600;
}
.button:hover {
  background-color: rgba(0, 165, 85, 0.6);
}
.button:focus {
  outline: 2px solid #087bef;
}
.button.disabled, .button[disabled] {
  background-color: #555;
  color: #888;
  cursor: default;
}

.hero {
  height: 100vh;
  background: #000;
  color: #fff;
  display: flex;
  justify-content: left;
  align-items: center;
}
.hero h2 {
  font-size: 200px;
}
.hero .line1 {
  padding-left: 50%;
}

.sticky {
  position: relative;
}

.sticky-bar {
  width: 100%;
  height: 0;
  position: sticky;
  top: calc(100% - 30px);
  z-index: 1000;
}

.sticky-bar-orange {
  background-color: orange;
  position: absolute;
  width: 1px;
  height: 30px;
  top: 0;
  left: 0;
}

.sticky-bar-black {
  background-color: #000;
  position: absolute;
  width: 100%;
  height: 30px;
  top: 0;
  left: 0;
}

.slide {
  position: relative;
  background: #000;
}

.bg-sticky {
  background-color: black;
  color: #555;
  font-size: 12px;
  height: 100vh;
  width: 100%;
  position: sticky;
  top: 0;
  overflow: hidden;
}

.slide1-slide {
  height: 100vh;
  width: 100%;
  color: #fff;
  font-size: 120px;
  position: relative;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.slide2 {
  padding: 100px 0;
  overflow: hidden;
}

.text {
  font-size: 100px;
  line-height: 1.3;
  font-weight: bold;
  color: #fff;
  transform-origin: left center;
}

.slide3 {
  position: relative;
}

.slide3-slide {
  position: sticky;
  height: 100vh;
  width: 100%;
  font-size: 100px;
  overflow: hidden;
  top: 0;
  color: #fff;
  text-align: center;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slide3-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.mosaic1 {
  height: 15%;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  transition: opacity ease-out 0.2s;
}

.slide3-img {
  height: 100vh;
  background-color: rgba(168, 243, 161, 0.1);
}

.slide4-slide {
  font-size: 100px;
  font-weight: bold;
  color: #fff;
  height: 100vh;
  display: flex;
  align-items: center;
}

.slide4-content {
  width: 60%;
  margin-top: -300px;
}

.slide4-sticky {
  width: 30%;
  position: sticky;
  top: 0;
  margin-left: 70%;
  font-size: 60px;
  color: #fff;
  padding: 100px 0;
}

