section,
.section {
  display: block;
  position: relative;
}

.container {
  display: block;
  position: relative;
  box-sizing: content-box;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 3%;
  padding-right: 3%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}
@media screen and (min-width: 768px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 30px;
    row-gap: 20px;
  }
}

.grid [class*=span] {
  margin-bottom: 5px;
}

.shade {
  background-color: #c2dce2;
}

.row {
  margin: 15px 0;
}

.span1 {
  grid-column: span 1;
}

